CAA4C;EACjE,cAAc,EAAE,gEAAgE;AAClF;;AAEA,uDAAuD;AACvD;EACE,gBAAgB;AAClB;;AAEA,4DAA4D;AAC5D;EACE,sBAAsB;EACtB,mBAAmB;EACnB,aAAa;EACb,mBAAmB;AACrB;;AAEA,uDAAuD;AACvD;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA,2EAA2E;AAC3E;;EAEE,SAAS;AACX;;AAEA,qDAAqD;AACrD;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA,gBAAgB;AAChB;;;;EAIE,gBAAgB;EAChB,eAAe;AACjB;;AAEA,eAAe;AACf;EACE,qBAAqB,EAAE,2DAA2D;EAClF,gBAAgB;EAChB,eAAe;AACjB;;AAEA,gCAAgC;AAChC;;EAEE,gBAAgB;EAChB,eAAe;AACjB","sourcesContent":["* {\n  padding: 0;\n  margin: 0;\n}\n \nbody {\n  font-family: 'Poppins', sans-serif;\n  background-color: white;\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n \n \n/*\n  AppBar\n*/\n \n.app-bar {\n  padding: 8px 16px;\n  background-color: white;\n  display: grid;\n  grid-template-columns: auto 1fr auto;\n  gap: 10px;\n  position: sticky;\n  top: 0;\n  z-index: 99;\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n}\n \n.app-bar .app-bar__menu {\n  display: flex;\n  align-items: center;\n}\n \n.app-bar .app-bar__menu button {\n  background-color: transparent;\n  border: none;\n  font-size: 18px;\n  padding: 8px;\n  cursor: pointer;\n}\n \n.app-bar .app-bar__brand {\n  display: flex;\n  align-items: center;\n}\n \n.app-bar .app-bar__brand h2 {\n  color: green;\n  text-transform: uppercase;\n  font-size: 20px;\n  user-select: none;\n}\n \n.app-bar .app-bar__navigation {\n  position: absolute;\n  top: 50px;\n  left: -180px;\n  width: 150px;\n  transition: all 0.3s;\n  padding: 8px;\n  background-color: white;\n  overflow: hidden;\n}\n\n\n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n \n/* mengatur 44x44 dimensi */\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  font-weight: bold;\n  font-size: 20px;\n}\n/* ----- */\n\n.app-bar__navigation ul li  a:hover {\n  color: #37e557; \n  transition: color 0.4s ease, font-weight 0.3s ease;\n}\n\n\n/* Jumbotron styles */\n.jumbotron {\n  padding: 5rem;\n  margin-bottom: 0;\n  /* background-image: url('../../dist/images/hero-image-small.jpg');  */\n  background-position: center;\n  text-align: center;\n}\n\n.jumbotron h1 {\n  font-size: 2em;\n  color: rgba(250, 252, 250, 0.767); \n  \n}\n\n.jumbotron p {\n  font-size: 0.8em;\n  color: rgba(254, 255, 254, 0.911); \n}\n \n/*\n  Main Content\n*/\n \nmain {\n  padding: 32px;\n  flex: 1;\n}\n \n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n \n.content .content__heading {\n  font-weight: normal;\n}\n \n \n/*\n  restaurant\n*/\n \n.restaurant {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n  margin: 32px 0;\n}\n \n \n/*\n  restaurant Item\n*/\n \n.restaurant-item {\n  width: 100%;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  overflow: hidden;\n}\n \n.restaurant-item__header {\n  position: relative;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\n  width: 100%;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: black;\n  color: white;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\n  margin-left: 10px;\n}\n \n.restaurant-item .restaurant-item__content {\n  padding: 16px;\n}\n \n.restaurant-item .restaurant-item__content h3 {\n  margin: 0 0 10px 0;\n}\n \n.restaurant-item .restaurant-item__content h3 a {\n  color: green;\n  text-decoration: none;\n}\n \n.restaurant-item .restaurant-item__content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4; /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n \n \n/*\n  restaurant\n*/\n \n.restaurant {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 800px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 18px 16px;\n}\n \n.restaurant .restaurant__poster {\n  width: 100%;\n  max-width: 400px;\n}\n \n.restaurant .restaurant__info h4 {\n  margin: 8px 0;\n}\n \n \n \n\n/* Footer styles */\n#footer {\n  background-color: black;\n  color: white;\n  padding: 1rem;\n  text-align: center;\n}\n\n.f-logo img{\n  width: 250px;\n  cursor: pointer;\n}\n.f-content i{\n  font-size: 28px;\n  color: rgb(252, 252, 252);\n  padding: 10px;\n  transition: 0.5s;\n  cursor: pointer;\n  \n}\n.f-content i:hover{\n  background: green;\n  color: white;\n  border-radius: 5px;\n}\n.text-light{\n  font-size: 1rem;\n}\n.c-content {\n  margin-top: 20px;\n  font-size: 1rem;\n}\n\n.c-content a {\n  color: white;\n  text-decoration: none;\n  font-weight: bold;\n}\n\n\n\n/* cardresto */\n.main-resto_container {\n  margin: 2em ;\n  font-size: 28px;\n  color: #0f0f0f; \n  background-color: #f8f9fa; \n  padding: 15px; /* Padding yang cukup untuk memberikan ruang di sekitar teks */\n  margin-bottom: 30px;\n}\n\n.main-resto_label {\n  margin: auto;\n  width: max-content;\n  font-size:large;\n  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;\n}\n\n.main-resto_label hr {\n  margin-top: 0;\n  width: 50%;\n  border-bottom: 1px solid green;\n}\n\n.list-resto {\n  margin-top: 50px;\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);  /* Menentukan 3 kolom sejajar */\n  grid-gap: 16px;\n}\n\n.resto-item {\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 15px;\n  overflow: hidden;\n  position: relative;\n  top: 0;\n  transition: top ease 0.4s;\n  cursor: pointer;\n  max-width: 400px;\n  width: 100%; \n}\n\n\n.resto-item:hover {\n  top: -4px;\n}\n\n.resto-title {\n  display: flex;\n  justify-content: space-between;\n}\n\n.resto-item a {\n  text-decoration: none; /* Menghapus underline dari tautan */\n  color: black; \n}\n\n.resto-item_image {\n  object-fit: cover;\n  width: 100%;\n  height: 250px;\n  margin-bottom: 15px;\n  border-radius: 15px 15px 0 0;\n}\n\n.resto-item_city,\n.resto-item_rating,\n.resto-item_name,\n.resto-item_desc {\n  padding: 0 16px;\n\n}\n\n.resto-item_city {\n  font-size: 16px;\n  text-transform: uppercase;\n  color: black;\n}\n\n.resto-item_rating {\n  color: black;\n  text-decoration: none;\n  margin: 0 0 0 5px;\n  padding: 2px 3px;\n  border-radius: 5px;\n  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;\n  \n}\n\n.resto-item_name {\n  font-weight: bold;\n  font-size: 24px;\n  margin-top: 16px;\n  transition: 0.3s opacity;\n  color: green;\n  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;\n}\n\n.resto-item_name:hover {\n  opacity: 0.7;\n}\n\n.resto-item_desc {\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 5;\n  font-weight: 500;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  margin: 16px 0;\n  font-size: 14px;\n  line-height: 1.5em;\n}\n\n.skip-link {\n  background-color: #37e557;\n  border-radius: 5px;\n  color: #0f0f0f;\n  font-weight: 500;\n  left: 0;\n  line-height: 44px;\n  margin: 0 auto;\n  padding: 0 10px;\n  position: absolute;\n  right: 0;\n  top: -50px;\n  transition: 0.5s;\n  width: max-content;\n  z-index: 1500;\n}\n\n.skip-link:focus {\n  top: 30px;\n  \n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #4ddb00;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* detail .restaurant */\n/* Gaya untuk elemen dengan kelas \"new-detail\" */\n.new-detail {\n  background-color: #f8f8f8;\n  padding: 20px;\n  border: 1px solid #ddd;\n  border-radius: 5px;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-container-info\" */\n.new-container-info {\n  display: flex;\n  flex-direction: column-reverse; /* Membalikkan urutan elemen menjadi vertikal */\n  align-items: center;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-resto-item_content\" */\n.new-resto-item_content {\n  margin-bottom: 20px; /* Memberikan ruang antara gambar dan detail-info */\n}\n\n.new-resto-item_content img {\n  max-width: 100%;\n  height: auto;\n  border-radius: 5px;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-detail-info\" */\n.new-detail-info {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-resto-name\" */\n.new-resto-name {\n  font-size: 1em;\n  font-weight: bold;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-resto-address\" */\n.new-resto-address {\n  font-style: italic;\n  color: #555;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-resto-rating\" */\n.new-resto-rating {\n  font-size: 1em;\n  color: #ff9800;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-detail-desc\" */\n.new-detail-desc {\n  font-size: 0.7em;\n  margin-top: 10px;\n  text-align: center;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-resto-category\" */\n.new-resto-category {\n  margin-top: 10px;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-restaurant-detail__menu-list\" */\n.new-restaurant-detail__menu-list {\n  display: flex;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-foods\" dan \"new-drinks\" */\n.new-foods,\n.new-drinks {\n  flex: 1; /* Menggunakan fleksibilitas untuk membagi ruang dengan proporsi yang sama */\n  padding: 0 20px; \n}\n\n/* Gaya untuk elemen dengan kelas \"new-restaurant-detail__foods\" dan \"new-restaurant-detail__drinks\" */\n.new-restaurant-detail__foods,\n.new-restaurant-detail__drinks {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n\n/* Gaya untuk elemen li di dalam \"new-restaurant-detail__foods\" dan \"new-restaurant-detail__drinks\" */\n.new-restaurant-detail__foods li,\n.new-restaurant-detail__drinks li {\n  margin-bottom: 10px; /* Menambahkan margin bawah di antara item */\n  display: block; /* Menjadikan elemen sebagai blok agar disusun secara vertikal */\n}\n\n/* Gaya untuk elemen dengan kelas \"new-detail-review\" */\n.new-detail-review {\n  margin-top: 20px;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-detail-review-item\" */\n.new-detail-review-item {\n  border: 1px solid #ddd;\n  border-radius: 10px;\n  padding: 15px;\n  margin-bottom: 15px;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-header-review\" */\n.new-header-review {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-name-review\" dan \"new-date-review\" */\n.new-name-review,\n.new-date-review {\n  margin: 0;\n}\n\n/* Gaya untuk elemen dengan kelas \"new-body-review\" */\n.new-body-review {\n  margin-top: 10px;\n}\n\n.restaurant-item__not__found {\n  margin-top: 30px;\n  text-align: center;\n}\n\n/* for button  */\nbutton,\ninput[type=\"button\"],\ninput[type=\"submit\"],\ninput[type=\"reset\"] {\n  min-height: 44px;\n  min-width: 44px;\n}\n\n/* for tautan */\na {\n  display: inline-block; /* Pastikan tautan dapat memiliki ukuran lebar dan tinggi */\n  min-height: 44px;\n  min-width: 44px;\n}\n\n/* for input teks and textarea */\ninput[type=\"text\"],\ntextarea {\n  min-height: 44px;\n  min-width: 44px;\n}\n"],"sourceRoot":""}]);
